.about-me p {
    font-size: 18px;
    font-family: "Open Sans", Helvetica, Arial, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", sans-serif;
    font-style: normal;
    color: #7a7b80;
    text-align: justify; /* Justifies the text so it aligns on both sides */
  }

  
.about-me {
  text-align: center;
  border-radius: 10px;
  padding: 20px;
  margin: 0 auto; /* Centers the content horizontally */
  max-width: 800px; /* Adjust this value based on your design needs */
  color: #333;
  line-height: 1.8;
}

.blog-container.dark .about-me {
  color: rgb(175, 128, 11);
}

.blog-container.light .about-me {
  color: rgb(162, 204, 235);
}